export interface SpeechRecognitionResult {
  text: string;
  confidence: number;
  isFinal: boolean;
}

class SpeechRecognitionService {
  private recognition: any;
  private isListening: boolean = false;
  private onResultCallback: ((result: SpeechRecognitionResult) => void) | null = null;
  private onErrorCallback: ((error: string) => void) | null = null;

  constructor() {
    // Initialize Web Speech API
    const SpeechRecognition = window.SpeechRecognition || window.webkitSpeechRecognition;
    if (!SpeechRecognition) {
      throw new Error('Speech recognition is not supported in this browser.');
    }

    this.recognition = new SpeechRecognition();
    this.recognition.continuous = false;
    this.recognition.interimResults = true;
    this.recognition.lang = 'en-US';

    this.setupRecognitionHandlers();
  }

  private setupRecognitionHandlers() {
    this.recognition.onresult = (event: any) => {
      if (!this.onResultCallback) return;

      const result = event.results[event.results.length - 1];
      this.onResultCallback({
        text: result[0].transcript,
        confidence: result[0].confidence,
        isFinal: result.isFinal
      });
    };

    this.recognition.onerror = (event: any) => {
      if (!this.onErrorCallback) return;
      
      let errorMessage = 'An error occurred during speech recognition.';
      switch (event.error) {
        case 'no-speech':
          errorMessage = 'No speech was detected. Please try again.';
          break;
        case 'audio-capture':
          errorMessage = 'No microphone was found or microphone is disabled.';
          break;
        case 'not-allowed':
          errorMessage = 'Microphone permission was denied. Please enable it to use this feature.';
          break;
        case 'network':
          errorMessage = 'Offline speech recognition is active.';
          break;
      }
      this.onErrorCallback(errorMessage);
      this.stopListening();
    };

    this.recognition.onend = () => {
      if (this.isListening) {
        // If we're still supposed to be listening, restart recognition
        this.recognition.start();
      }
    };
  }

  async startListening(
    onResult: (result: SpeechRecognitionResult) => void,
    onError: (error: string) => void
  ): Promise<void> {
    if (this.isListening) {
      onError('Already listening');
      return;
    }

    try {
      this.onResultCallback = onResult;
      this.onErrorCallback = onError;
      this.isListening = true;
      this.recognition.start();
    } catch (error) {
      const errorMessage = error instanceof Error ? error.message : String(error);
      console.error('Failed to start recording:', error);
      onError(`Failed to start recording: ${errorMessage}`);
    }
  }

  stopListening(): void {
    if (this.isListening) {
      this.isListening = false;
      this.recognition.stop();
    }
  }
}

export const speechService = new SpeechRecognitionService(); 