import React from 'react';
import { Box } from '@mui/material';
import { motion } from 'framer-motion';

const AnimatedBackground = () => {
  return (
    <Box
      sx={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        bottom: 0,
        overflow: 'hidden',
        zIndex: -1,
        background: 'linear-gradient(45deg, #000428 0%, #004e92 100%)',
      }}
    >
      {/* Animated mesh gradient circles */}
      {[...Array(5)].map((_, index) => (
        <motion.div
          key={index}
          style={{
            position: 'absolute',
            width: '40vw',
            height: '40vw',
            borderRadius: '50%',
            background: `radial-gradient(circle, rgba(${
              Math.sin(index) * 255
            }, ${Math.cos(index) * 255}, 255, 0.15) 0%, rgba(0,0,0,0) 70%)`,
            filter: 'blur(60px)',
          }}
          animate={{
            x: [
              `${Math.sin(index) * 20}vw`,
              `${Math.cos(index) * 20}vw`,
              `${Math.sin(index) * 20}vw`,
            ],
            y: [
              `${Math.cos(index) * 20}vh`,
              `${Math.sin(index) * 20}vh`,
              `${Math.cos(index) * 20}vh`,
            ],
            scale: [1, 1.2, 1],
          }}
          transition={{
            duration: 15 + index * 2,
            repeat: Infinity,
            ease: 'linear',
          }}
        />
      ))}

      {/* Animated grid overlay */}
      <Box
        sx={{
          position: 'absolute',
          top: 0,
          left: 0,
          right: 0,
          bottom: 0,
          backgroundImage: `
            linear-gradient(rgba(255,255,255,0.05) 1px, transparent 1px),
            linear-gradient(90deg, rgba(255,255,255,0.05) 1px, transparent 1px)
          `,
          backgroundSize: '50px 50px',
          perspective: '1000px',
          transform: 'rotateX(60deg) scale(2.5)',
          opacity: 0.3,
        }}
      />
    </Box>
  );
};

export default AnimatedBackground; 