const API_ENDPOINT = 'https://api.fireworks.ai/inference/v1/chat/completions';
const MODEL_PATH = 'accounts/fireworks/models/deepseek-r1';

export interface AccentAnalysisResult {
  score: number;
  feedback: string;
  detailedFeedback: {
    pronunciation: string;
    intonation: string;
    rhythm: string;
    suggestions: string[];
  };
}

function calculateSimilarity(str1: string, str2: string): number {
  const s1 = str1.toLowerCase();
  const s2 = str2.toLowerCase();

  // Calculate Levenshtein distance
  const matrix = Array(s2.length + 1).fill(null).map(() => Array(s1.length + 1).fill(null));

  for (let i = 0; i <= s1.length; i += 1) {
    matrix[0][i] = i;
  }

  for (let j = 0; j <= s2.length; j += 1) {
    matrix[j][0] = j;
  }

  for (let j = 1; j <= s2.length; j += 1) {
    for (let i = 1; i <= s1.length; i += 1) {
      const indicator = s1[i - 1] === s2[j - 1] ? 0 : 1;
      matrix[j][i] = Math.min(
        matrix[j][i - 1] + 1,
        matrix[j - 1][i] + 1,
        matrix[j - 1][i - 1] + indicator
      );
    }
  }

  // Convert distance to similarity score (0-100)
  const maxLength = Math.max(s1.length, s2.length);
  const distance = matrix[s2.length][s1.length];
  return Math.max(0, Math.min(100, Math.round((1 - distance / maxLength) * 100)));
}

function analyzeWords(original: string, spoken: string): { 
  correctWords: string[],
  incorrectWords: string[],
  missingWords: string[],
  extraWords: string[]
} {
  const originalWords = original.toLowerCase().split(/\s+/);
  const spokenWords = spoken.toLowerCase().split(/\s+/);
  
  const correctWords: string[] = [];
  const incorrectWords: string[] = [];
  
  // Find correct and incorrect words
  spokenWords.forEach(word => {
    if (originalWords.includes(word)) {
      correctWords.push(word);
    } else {
      incorrectWords.push(word);
    }
  });
  
  // Find missing words
  const missingWords = originalWords.filter(word => !spokenWords.includes(word));
  
  // Find extra words
  const extraWords = spokenWords.filter(word => !originalWords.includes(word));
  
  return {
    correctWords,
    incorrectWords,
    missingWords,
    extraWords
  };
}

export async function analyzeAccent(
  originalText: string,
  spokenText: string
): Promise<AccentAnalysisResult> {
  try {
    console.log('Starting offline accent analysis...');
    console.log('Original text:', originalText);
    console.log('Spoken text:', spokenText);

    const similarityScore = calculateSimilarity(originalText, spokenText);
    const wordAnalysis = analyzeWords(originalText, spokenText);
    
    // Generate feedback based on analysis
    const feedback = generateFeedback(similarityScore, wordAnalysis);
    
    // Generate detailed feedback
    const detailedFeedback = {
      pronunciation: generatePronunciationFeedback(wordAnalysis),
      intonation: generateIntonationFeedback(similarityScore),
      rhythm: generateRhythmFeedback(originalText, spokenText),
      suggestions: generateSuggestions(wordAnalysis, similarityScore)
    };

    return {
      score: similarityScore,
      feedback,
      detailedFeedback
    };
  } catch (error) {
    console.error('Error in accent analysis:', error);
    throw new Error('Unable to analyze accent. Please try again.');
  }
}

function generateFeedback(score: number, analysis: ReturnType<typeof analyzeWords>): string {
  if (score >= 90) {
    return 'Excellent pronunciation! Your speech was very clear and accurate.';
  } else if (score >= 75) {
    return 'Good job! Your pronunciation is clear, with some minor areas for improvement.';
  } else if (score >= 60) {
    return 'Fair attempt. Keep practicing to improve your pronunciation accuracy.';
  } else {
    return 'Continue practicing. Focus on pronouncing each word clearly and at a steady pace.';
  }
}

function generatePronunciationFeedback(analysis: ReturnType<typeof analyzeWords>): string {
  const { correctWords, incorrectWords, missingWords } = analysis;
  
  if (incorrectWords.length === 0 && missingWords.length === 0) {
    return 'Excellent pronunciation of all words!';
  }
  
  let feedback = '';
  
  if (incorrectWords.length > 0) {
    feedback += `Words to practice: ${incorrectWords.join(', ')}. `;
  }
  
  if (missingWords.length > 0) {
    feedback += `Missing words: ${missingWords.join(', ')}. `;
  }
  
  return feedback || 'Focus on clear pronunciation of each word.';
}

function generateIntonationFeedback(score: number): string {
  if (score >= 90) {
    return 'Natural and expressive intonation patterns.';
  } else if (score >= 75) {
    return 'Good intonation with room for more natural expression.';
  } else if (score >= 60) {
    return 'Work on varying your tone to sound more natural.';
  } else {
    return 'Focus on the rise and fall of your voice to match natural speech patterns.';
  }
}

function generateRhythmFeedback(original: string, spoken: string): string {
  const originalWords = original.split(/\s+/).length;
  const spokenWords = spoken.split(/\s+/).length;
  
  if (Math.abs(originalWords - spokenWords) <= 1) {
    return 'Good speech rhythm and pacing.';
  } else if (spokenWords > originalWords) {
    return 'Try to speak more concisely, matching the original pace.';
  } else {
    return 'Try to include all words while maintaining a steady pace.';
  }
}

function generateSuggestions(analysis: ReturnType<typeof analyzeWords>, score: number): string[] {
  const suggestions: string[] = [];
  
  if (analysis.incorrectWords.length > 0) {
    suggestions.push(`Practice pronouncing these words: ${analysis.incorrectWords.join(', ')}`);
  }
  
  if (analysis.missingWords.length > 0) {
    suggestions.push('Make sure to pronounce all words in the phrase');
  }
  
  if (score < 75) {
    suggestions.push('Try speaking more slowly and clearly');
  }
  
  if (suggestions.length === 0) {
    suggestions.push('Continue practicing to maintain your excellent pronunciation');
  }
  
  return suggestions;
} 