import React, { useState } from 'react';
import {
  AppBar,
  Box,
  Button,
  Drawer,
  IconButton,
  List,
  ListItem,
  ListItemButton,
  ListItemIcon,
  ListItemText,
  Toolbar,
  Typography,
  Divider,
  useMediaQuery,
  useTheme,
} from '@mui/material';
import MenuIcon from '@mui/icons-material/Menu';
import HomeIcon from '@mui/icons-material/Home';
import LoginIcon from '@mui/icons-material/Login';
import PersonAddIcon from '@mui/icons-material/PersonAdd';
import SportsEsportsIcon from '@mui/icons-material/SportsEsports';
import AssessmentIcon from '@mui/icons-material/Assessment';
import GroupIcon from '@mui/icons-material/Group';
import { Link, useLocation, useNavigate } from 'react-router-dom';

const menuItems = [
  { text: 'Home', icon: <HomeIcon />, path: '/' },
  { text: 'Practice', icon: <SportsEsportsIcon />, path: '/practice' },
  { text: 'Progress', icon: <AssessmentIcon />, path: '/progress' },
  { text: 'Community', icon: <GroupIcon />, path: '/community' },
];

const Navbar = () => {
  const [mobileOpen, setMobileOpen] = useState(false);
  const theme = useTheme();
  const isMobile = useMediaQuery(theme.breakpoints.down('sm'));
  const location = useLocation();
  const navigate = useNavigate();

  const handleDrawerToggle = () => {
    setMobileOpen(!mobileOpen);
  };

  const drawer = (
    <Box sx={{
      background: 'linear-gradient(45deg, rgba(0,242,254,0.1) 30%, rgba(79,172,254,0.1) 90%)',
      height: '100%',
      display: 'flex',
      flexDirection: 'column',
    }}>
      <Box
        sx={{
          display: 'flex',
          justifyContent: 'center',
          alignItems: 'center',
          p: 2,
        }}
      >
        <img src="/lpu-logo.svg" alt="LPU Logo" style={{ height: 60 }} />
      </Box>
      <List sx={{ flexGrow: 1 }}>
        {menuItems.map((item) => (
          <ListItem key={item.text} disablePadding>
            <ListItemButton
              selected={location.pathname === item.path}
              onClick={() => navigate(item.path)}
            >
              <ListItemIcon sx={{ color: 'rgba(255,255,255,0.9)' }}>
                {item.icon}
              </ListItemIcon>
              <ListItemText primary={item.text} />
            </ListItemButton>
          </ListItem>
        ))}
      </List>
      <Box sx={{ p: 2 }}>
        <Button
          fullWidth
          component={Link}
          to="/signin"
          startIcon={<LoginIcon />}
          sx={{
            mb: 1,
            color: 'rgba(255,255,255,0.9)',
            borderColor: 'rgba(255,255,255,0.5)',
            '&:hover': {
              borderColor: 'rgba(255,255,255,0.8)',
              backgroundColor: 'rgba(255,255,255,0.1)',
            },
          }}
          variant="outlined"
        >
          Sign In
        </Button>
        <Button
          fullWidth
          component={Link}
          to="/signup"
          startIcon={<PersonAddIcon />}
          sx={{
            background: 'linear-gradient(45deg, #00f2fe 30%, #4facfe 90%)',
            color: 'white',
            '&:hover': {
              background: 'linear-gradient(45deg, #00d8e0 30%, #4f9ce0 90%)',
            },
          }}
        >
          Sign Up
        </Button>
      </Box>
    </Box>
  );

  return (
    <Box sx={{ flexGrow: 1 }}>
      <AppBar
        position="fixed"
        sx={{
          background: 'linear-gradient(45deg, rgba(0,242,254,0.2) 30%, rgba(79,172,254,0.2) 90%)',
          backdropFilter: 'blur(10px)',
          borderBottom: '1px solid rgba(255,255,255,0.1)',
        }}
      >
        <Toolbar>
          <IconButton
            color="inherit"
            aria-label="open drawer"
            edge="start"
            onClick={handleDrawerToggle}
            sx={{ mr: 2, display: { sm: 'none' } }}
          >
            <MenuIcon />
          </IconButton>
          
          <img src="/lpu-logo.svg" alt="LPU Logo" style={{ height: 40, marginRight: 16 }} />

          <Typography
            variant="h6"
            noWrap
            component={Link}
            to="/"
            sx={{
              flexGrow: 1,
              textDecoration: 'none',
              color: 'rgba(255,255,255,0.9)',
              fontWeight: 'bold',
              textShadow: '0 2px 4px rgba(0,0,0,0.2)',
              '&:hover': {
                color: 'rgba(255,255,255,1)',
              },
            }}
          >
            Accent Trainer
          </Typography>

          <Box sx={{ display: { xs: 'none', sm: 'flex' }, gap: 1 }}>
            {menuItems.map((item) => (
              <Button
                key={item.text}
                color="inherit"
                startIcon={item.icon}
                onClick={() => navigate(item.path)}
                sx={{
                  backgroundColor: location.pathname === item.path ? 'rgba(255, 255, 255, 0.1)' : 'transparent',
                  '&:hover': {
                    backgroundColor: 'rgba(255, 255, 255, 0.2)',
                  },
                }}
              >
                {item.text}
              </Button>
            ))}
            <Button
              color="inherit"
              startIcon={<LoginIcon />}
              onClick={() => navigate('/signin')}
              sx={{
                backgroundColor: location.pathname === '/signin' ? 'rgba(255, 255, 255, 0.1)' : 'transparent',
                '&:hover': {
                  backgroundColor: 'rgba(255, 255, 255, 0.2)',
                },
              }}
            >
              Sign In
            </Button>
            <Button
              color="inherit"
              startIcon={<PersonAddIcon />}
              onClick={() => navigate('/signup')}
              sx={{
                backgroundColor: location.pathname === '/signup' ? 'rgba(255, 255, 255, 0.1)' : 'transparent',
                '&:hover': {
                  backgroundColor: 'rgba(255, 255, 255, 0.2)',
                },
              }}
            >
              Sign Up
            </Button>
          </Box>
        </Toolbar>
      </AppBar>
      <Box
        component="nav"
        sx={{ width: { sm: 240 }, flexShrink: { sm: 0 } }}
      >
        <Drawer
          variant="temporary"
          open={mobileOpen}
          onClose={handleDrawerToggle}
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          sx={{
            display: { xs: 'block', sm: 'none' },
            '& .MuiDrawer-paper': {
              boxSizing: 'border-box',
              width: 240,
              backgroundColor: 'rgba(0,0,0,0.9)',
              backdropFilter: 'blur(10px)',
            },
          }}
        >
          {drawer}
        </Drawer>
      </Box>
      {/* Add spacing below the fixed AppBar */}
      <Toolbar />
    </Box>
  );
};

export default Navbar; 